import React from 'react'

function Profile() {
    return (
        <div className="profile px-2 mt-5 mb-2 d-flex justify-content-start align-items-center">
             <div className="d-flex align-items-center px-2"><img style={{width:'50px'}} className="rounded-circle"
              src="https://source.unsplash.com/60x60/" alt=""/></div>
                <div className="d-flex justify-content-center align-items-start flex-column ">
                    <strong>Hi Reader,</strong>
                    <span>Here's your News</span>
                </div>
        </div>
    )
}

export default Profile
