import React from 'react'


function Rowcard({title,summary,published,link,mainPost,setPosts,id}) {

    const removePost=(id)=>{
        const update=mainPost.filter((post)=>(id!==post.id))
        setPosts(update)
    }
   
    return (
        <>
    <div className="d-flex align-items-center cardrow" > 
      
       <div className="p-2 m-1 card-data " data-toggle="modal" data-target=".bd-example-modal-lg">
       <div className="d-flex align-items-center px-2"><img style={{width:'50px'}} 
       className="rounded-circle" src="https://source.unsplash.com/60x60/?computer" alt=""/></div>
       <div className="d-flex flex-column overflow-hidden">
          <strong className="textflow">{title}</strong>
          <span className="textflow" >{summary}</span>
          <span className="text-muted textflow">{published}</span>
       </div>
    
    
</div>
<div className=" bg-white rounded-circle ml-1 p-2" onClick={()=>removePost(id)}>
    <div className="closebtn bg-white rounded-circle pl-1"></div>
</div>
    </div>
       
    <div className="modal fade bd-example-modal-lg" tabIndex="-1" role="dialog" aria-labelledby="myLargeModalLabel" aria-hidden="true">
  <div className="modal-dialog modal-lg">
    <div className="modal-content">
        <div className="embed-responsive embed-responsive-16by9">
  <iframe title={published} className="embed-responsive-item" src={link} allowFullScreen></iframe>
</div>
    </div>
  </div>
</div>
       </>
    )
}

export default Rowcard
