import React,{useState} from 'react'
import '../App.css'

function Form() {
    const [name,setName]=useState();
    const [lastName,setLastName]=useState();
    const [address,setAddress]=useState();
    const [country,setCountry]=useState();
    const [email,setEmail]=useState();
    const [number,setNumber]=useState();

    const showdata=()=>{
        alert('your name is'+name+''+lastName+' your address'+address+''+country+''+email+''+number)
    }
    const setValue=(e,setter)=>{
        setter(e.target.value);
    }

    return (
        <div className="container px-5 w-50 mt-5">
            <div className="mb-4">
                    <h5 className="form-head">Thank you so much for taking the time!</h5>
                    <small className="form-sub">Please provide the below details</small>
            </div>
            <form action="" className="form">
                <div className="form-group ">
                    <label htmlFor="name">First Name</label>
                        <input className="form-control w-50 shadow" 
                        type="text" 
                        placeholder="John"
                        value={name}
                        onChange={(e)=>setValue(e,setName)}
                        />
                   
                </div>
                <div className="form-group">
                    <label htmlFor="lname">Last Name</label>
                        <input className="form-control w-50 shadow" type="text"
                         placeholder="Doe"
                         value={lastName}
                         onChange={(e)=>setValue(e,setLastName)}
                         />
                   
                </div>
                <div className="form-group">
                    <label htmlFor="address">Address</label>
                        <textarea rows="4" className="form-control shadow" type="text"
                         placeholder="Enter your full Postal Address"
                         value={address}
                         onChange={(e)=>setValue(e,setAddress)}
                         />
                </div>
                <div className="form-group">
                    <label htmlFor="country">Country</label>
                        <input row="" col="" className="form-control w-50 shadow" type="text"
                         placeholder="India"
                         value={country}
                         onChange={(e)=>setValue(e,setCountry)}
                         />
                </div>
                <div className="form-group">
                    <label htmlFor="Email">Email ID</label>
                        <input  className="form-control w-50 shadow" type="gmail"
                         placeholder="example@sample.com"
                         value={email}
                         onChange={(e)=>setValue(e,setEmail)}
                         />
                </div>
                <div className="form-group">
                    <label htmlFor="phone">Phone Number</label>
                    
                      <div className="d-flex">
                      <input className="no rounded" style={{width:'40px'}} type="tel" placeholder="+91"/>
                      <input type="tel" className="form-control w-50" placeholder="111-111-111"/> 
                      </div>
                       
                </div>
                
                <div className="form-group">

                </div>
                <button 
                onClick={()=>{showdata()}}
                className="btn btntext btn-default px-5 py-2 rounded btnfeed shadow">
                   Submit 
           </button>
            </form>
        </div>
    )
}

export default Form
