import React from 'react'

function Feedback({feedback,setfeedback}) {
    return (
        <div className="feedback m-2 text-center">
           <h4 className="p-2">Have a Feedback?</h4>
           <button onClick={()=>{setfeedback(!feedback)}} className={`${feedback?'btn btn-default px-5 py-2 rounded btnfeed2':'btn btn-default px-5 py-2 rounded btnfeed'}`}>
               <span className="font-weight-bold" >We're Listening</span>
           </button>
        </div>
    )
}

export default Feedback
