import React from 'react';
import Card from './Card';
import Rowcard from './Rowcard'

const Datas = ({ posts, loading,toggle,mainPost,setPosts }) => {
  if (loading) {
    return <h2>Loading...</h2>;
  }

  return (
   <>
   {
     toggle?(posts.map((post,i)=>(
        
          <Card title={post.title}
              summary={post.summary}
              published={post.published}
              link={post.link}
              posts={posts}
              mainPost={mainPost}
              setPosts={setPosts}
              key={i}
              id={post.id}
        />
        
     ))):(posts.map((post,i)=>(
       
         <Rowcard
            title={post.title}
              summary={post.summary}
              published={post.published}
              link={post.link}
              posts={posts}
              mainPost={mainPost}
              setPosts={setPosts}
              key={i}
              id={post.id}
         />
      
         
      )))
   }
   </>
  );

};

export default Datas;
