import React from 'react'

function Toggle({toggle,setToggle}) {
   
    return (
        <div className=" my-3 toggle d-flex flex-column justify-content-center align-items-center">
            <h4>View Toggle</h4>
            <div className="tbox" style={{fontSize:'36px'}}>
                <i className={`${toggle?'fa fa-list-alt  active':'fa fa-list-alt  togglebg'}`}
                   onClick={()=>{setToggle(true)}}
                ></i>
                <i onClick={()=>{setToggle(false)}}
                className={`${toggle?'fa fa-list  togglebg':'fa fa-list  active'}`}></i></div>
            
        </div>
    )
}

export default Toggle
