import React from 'react'

function Card({title,summary,published,link,mainPost,setPosts,id}) {
    
    const removePost=(id)=>{
        const update=mainPost.filter((post)=>(id!==post.id))
        setPosts(update)
    }

    return (
        <>
        <div className="p-0 m-0s" >
        <div  className="p-2 d-flex flex-column justify-content-center card-data2 shadow">
        <div  className="closebtn p-2" onClick={()=>{removePost(id)}}></div>

        <div data-toggle="modal" data-target=".bd-example-modal-lg">
        <div className="d-flex flex-column" style={{height:'40%'}}>
            <strong className="textflow">{title}</strong>
              <span className="textflow" >{summary}</span>
              <span className="text-muted textflow">{published}</span>
           </div> 
          <img alt="thumbnail" src="https://source.unsplash.com/120x60/" className="mt-4" style={{width:'100%',height:'100px'}}></img>
        </div>
            
        </div>
        </div>
        
        

<div className="modal fade bd-example-modal-lg" tabIndex="-1" role="dialog" aria-labelledby="myLargeModalLabel" aria-hidden="true">
  <div className="modal-dialog modal-lg">
    <div className="modal-content">
        <div className="embed-responsive embed-responsive-16by9">
  <iframe title={published} 
  className="embed-responsive-item" src={link} allowFullScreen></iframe>
</div>
    </div>
  </div>
</div>
        </>
    )
}

export default Card
